import React, { Component } from 'react';
import spfc from '../assets/spfc1.png';
import adidas from '../assets/adidas.png';
import roku from '../assets/roku.png';
import volvo from '../assets/volvo.png';
import konami from '../assets/konami.png'
import face from '../assets/face.png';
import twitter from '../assets/twitter2.png';
import insta from '../assets/insta2.png';
import youtube from '../assets/youtube.png';
import '../footer.css';


class Footer extends Component {

    render() {
        return (
            <div style={{flex:1, height:710, backgroundColor:'black', marginTop:130}}>
                <img src={spfc} style={{width:130, height:130,  position: 'absolute', bottom:-4360, left:570, zIndex:1}}></img>
                <div>
                    <h5 id='texto'>PARCEIROS</h5>
                </div>
                <div class='sponsor'>
                    <a href='https://www.adidas.com.br/sao_paulo'>
                        <img src={adidas}></img>
                    </a>
                    <a href='https://www.roku.com/pt-br/'>
                        <img src={roku} style={{ height:40, alignSelf:'center', marginLeft:70, marginTop:40}}></img>
                    </a>
                    <a href='https://www.volvocars.com/br'>
                        <img src={volvo} style={{width:180, height:30, alignSelf:'center', marginLeft:70, marginTop:45}}></img>
                    </a>
                </div>
                <div class='sponsor'>
                    <a href='https://www.konami.com/wepes/2021/us/pt-br/pc/'>
                         <img src={konami} style={{width:180, height:30, alignSelf:'center', marginTop:15}}></img>
                    </a>
                    <a href='https://www.vrcollezioni.com.br'>
                        <img src='https://www.shoppingbougainville.com/wp-content/uploads/2020/11/VR.png' style={{width:190, height:100, alignSelf:'center', marginTop:15, marginLeft:25}}></img>
                    </a>
                </div>
                <br></br>
                <br></br>
                <h5 id='texto2'>SIGA O SO PAULO FC NAS REDES SOCIAIS:</h5>
                <div style={{alignItems:'center', textAlign:'center', marginRight:30, marginTop:50}}>
                    <a href="https://www.facebook.com/saopaulofc/"> 
                        <img src={face} style={{width:55, height:55, marginLeft:20}}></img>
                    </a>
                    <a href="https://twitter.com/SaoPauloFC/">
                        <img src={twitter} style={{width:52, height:50, marginLeft:20}}></img>
                    </a>
                    <a href="https://www.instagram.com/saopaulofc/">
                        <img src={insta} style={{width:55, height:53, marginLeft:20}}></img>
                    </a>
                    <a href='https://www.youtube.com/spfctv'>
                        <img src={youtube} style={{width:55, height:55, marginLeft:20}}></img>
                    </a>
                </div>
                <div>
                    <br></br>
                    <p style={{color:'white', textAlign:'center', fontSize:12, marginTop:10}}>Copyright  2021 SoPauloFC.com.br All rights reserved. Do not duplicate or redistribute in any form.</p>
                </div>
            </div>
        )
    }
}
export default Footer;