import React, { Component } from 'react';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import lbt from '../assets/lbt.png';
import paulista from '../assets/paulista.png';
import '../trofeu.css';

class Login extends Component {
        render() {
            const responsive = {
                desktop: {
                    breakpoint: { max: 3000, min: 1024 },
                    items: 4,
                    slidesToSlide: 3 // optional, default to 1.
                  },
                  tablet: {
                    breakpoint: { max: 1024, min: 464 },
                    items: 2,
                    slidesToSlide: 2 // optional, default to 1.
                  },
                  mobile: {
                    breakpoint: { max: 464, min: 0 },
                    items: 1,
                    slidesToSlide: 1 // optional, default to 1.
                }
              };
                return (
                    <Carousel responsive={responsive}
                    >
                    <div class="item1">
                    <img src='https://www.santosfc.com.br/wp-content/uploads/2017/08/20170321_santos_trofeus_PEGX1561-Campeonato_Brasileiro_2004.png'></img>
                        <h1>6</h1>
                        <h6>Campeonatos Brasileiros</h6>
                    </div>
                    <div class='item2'>
                      <div class="linha-vertical">
                        <div style={{ color:'black', marginLeft:50,}}>
                            <div>
                            <img src='https://4.bp.blogspot.com/-eU70PcasEto/Wif7B7z79lI/AAAAAAAAjWs/JzRIIdsvg3omYFUz2NZR7XbshCl9HcSUwCLcBGAs/s1600/trof%25C3%25A9u.png'></img>
                            <h1>3</h1>
                            <h6>Mundial de Clubes</h6>
                            </div>
                        </div>
                      </div>
                    </div>
                    <div class='item3'>
                      <div class="linha-vertical">
                        <div style={{marginLeft:40, color:'black'}}>
                        <img src='https://upload.wikimedia.org/wikipedia/commons/thumb/7/7a/328-3287452_copa-libertadores-primer-trofeo-hd-png-download.png/249px-328-3287452_copa-libertadores-primer-trofeo-hd-png-download.png'></img>
                            <h1>3</h1>
                            <h6>Taa Libertadores da merica</h6>
                        </div>
                      </div>
                    </div>
                    <div class='item4'>
                      <div class="linha-vertical">
                        <div style={{marginLeft:50, color:'black'}}>
                            <img src='https://i.pinimg.com/originals/ef/bd/da/efbddafb3240e58d9bbaa1896dca43b9.png'></img>
                            <h1>2</h1>
                            <h6>Recopa Sulamericana</h6>
                        </div>
                      </div>
                    </div>
                    <div class='item5'>
                      <div class="linha-vertical">
                        <div style={{marginLeft:30, color:'black'}}>
                            <img src={lbt}></img>
                            <h1>6</h1>
                            <h6>SuperCopa da Libertadores</h6>
                        </div>
                      </div>
                      
                    </div>
                    <div class='item6'>
                      <div class="linha-vertical">
                        <div style={{marginLeft:30, color:'black'}}>
                            <img src={paulista}></img>
                            <h1>6</h1>
                            <h6>Campeonato Paulista</h6>
                        </div>
                      </div>
                      
                    </div>
                  </Carousel>

                );
        }
}
export default Login;